import Page from '@/components/Page'

export default {
  components: {
    Page
  },
  data () {
    return {
      body: null,
      title: null
    }
  },
  methods: {
    async submit () {
      this.$nuxt.$loading.start()
      await this.$db.stories.add({
        title: this.title,
        body: this.body
      })
      await this.$axios.$post('/stories', {
        user_id: this.$store.state.user.id,
        title: this.title,
        body: this.body
      })
      this.$router.push('/user/stories')
      this.$nuxt.$loading.finish()
    }
  }
}
